.latest-carousel {
  overflow-x: auto;
  white-space: nowrap;
  padding: 20px 0;
  cursor: grab;
  user-select: none;
}

.latest-carousel::-webkit-scrollbar {
  display: none;
}

.carousel-track {
  display: flex;
  gap: 20px;
  transform: translate3d(0, 0, 0); 
}

.carousel-item {
  flex: 0 0 auto;
  width: 280px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  will-change: transform;
}

.carousel-item img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  display: block;
  -webkit-user-drag: none; 
  user-select: none; 
}

.carousel-caption {
  padding: 10px;
  background: white;
  pointer-events: none; 
}

.carousel-caption h4 {
  font-size: 1rem;
  font-weight: bold;
}

.carousel-caption p {
  font-size: 0.875rem;
  color: #555;
}

.latest-carousel.dragging {
  cursor: grabbing;
  cursor: -webkit-grabbing;
}